
@mixin debug($size:1px, $color:red) {
  @if $is_debug {
    border: $size solid $color;
  }
}


@mixin image-desaturate($amount:100%){
  -webkit-filter: grayscale($amount);
  -moz-filter:    grayscale($amount);
  -ms-filter:     grayscale($amount);
  -o-filter:      grayscale($amount);
  filter: gray; 
}