$is_debug: false;

$spacer: 15px;
$color1: black;
$font-family1: 'Dosis', sans-serif;
$font-family2: 'Poppins', sans-serif;


body.page-template-homepage2 {
  // background-color: red !important;

  nav {
    background-color: transparent !important;
    color:black;
    .menu-item {
      a.nav-link {
        color: black;
        font-size: 2em;
        font-family: $font-family2;
        font-weight: 100;
      }
    }
  }
  
  #works {
    .work {
      @include debug;
      a:hover {
        display: block;
        transition: all 0.3s;
        transform: scale(1.05) rotate(1deg);
      }
      .wrapper {
        @include debug(1px, green);
        h2 {
          width: 200px;
          position: absolute;
          top: 100px;
          left: -50px;
          z-index: 1;
          font-family: $font-family2;
          font-weight: 500;
          font-size: 2em;
          //mix-blend-mode: difference;
          background-color: rgba($color: white, $alpha: .7);
          padding: $spacer/2;
          transform: rotate(-5deg);
        }
        img {
          width: 600px;
          @include image-desaturate();
        }
      }
      &:nth-child(even) {
        @include debug(1px, purple);
        margin-left: 50%;
        h2 {
          right: -50px;
        }
      }
    }
  }

}


div.skewed {
  position: relative;
  height: 400px;
  transform: skew(-2deg) rotate(2deg);
  -webkit-transform: skew(-2deg) rotate(2deg);
  -moz-transform: skew(-2deg) rotate(2deg);
  overflow: hidden;
}

div.skewed > * {
  width: 110%;
  position: absolute;
  top: 50%;
  transform: skew(2deg) rotate(-2deg) translateY(-50%);
  -webkit-transform: skew(2deg) rotate(-2deg) translateY(-50%);
  -moz-transform: skew(2deg) rotate(-2deg) translateY(-50%);
}

